import React, { useEffect, useRef, useState } from 'react';

const YouTubeAudioPlayer = ({ videoId }) => {
  const playerRef = useRef(null);
  const [isIframeReady, setIsIframeReady] = useState(false);
  const [error, setError] = useState(null);

  // Load the YouTube IFrame Player API
  useEffect(() => {
    // Load the YouTube IFrame API script
    const tag = document.createElement('script');
    tag.src = "https://www.youtube.com/iframe_api";
    const firstScriptTag = document.getElementsByTagName('script')[0];
    firstScriptTag.parentNode.insertBefore(tag, firstScriptTag);

    // Function called by the YouTube API when ready
    window.onYouTubeIframeAPIReady = () => {
      playerRef.current = new window.YT.Player(`youtube-audio-player-${videoId}`, {
        height: '0',
        width: '0',
        videoId: videoId,
        playerVars: {
          'playsinline': 1,
          'controls': 0,
          'enablejsapi': 1,
          'autoplay': 0,
        },
        events: {
          'onReady': onPlayerReady,
          'onError': onPlayerError,
        },
      });
    };

    // Cleanup on unmount
    return () => {
      if (playerRef.current) {
        playerRef.current.destroy();
      }
      delete window.onYouTubeIframeAPIReady;
    };
  }, [videoId]);

  const onPlayerReady = (event) => {
    setIsIframeReady(true);
    console.log('YouTube Player is ready');
  };

  const onPlayerError = (event) => {
    setError('Error loading YouTube video.');
    console.error('YouTube Player Error:', event.data);
  };

  const playVideo = () => {
    if (playerRef.current && isIframeReady) {
      playerRef.current.playVideo();
    }
  };

  const pauseVideo = () => {
    if (playerRef.current && isIframeReady) {
      playerRef.current.pauseVideo();
    }
  };

  return (
    <div style={{ display: 'flex', flexDirection: 'column', gap: '10px', marginTop: '10px' }}>
      {/* YouTube Audio Player */}
      <div id={`youtube-audio-player-${videoId}`}></div>

      {/* Play and Pause Buttons */}
      <div style={{ display: 'flex', gap: '10px' }}>
        <button
          onClick={playVideo}
          disabled={!isIframeReady}
          style={{
            padding: '10px 20px',
            backgroundColor: isIframeReady ? '#00aaff' : '#a0a0a0',
            color: '#ffffff',
            border: 'none',
            borderRadius: '5px',
            cursor: isIframeReady ? 'pointer' : 'not-allowed',
            opacity: isIframeReady ? 1 : 0.6,
          }}
        >
          Play Audio
        </button>
        <button
          onClick={pauseVideo}
          disabled={!isIframeReady}
          style={{
            padding: '10px 20px',
            backgroundColor: isIframeReady ? '#ff5555' : '#a0a0a0',
            color: '#ffffff',
            border: 'none',
            borderRadius: '5px',
            cursor: isIframeReady ? 'pointer' : 'not-allowed',
            opacity: isIframeReady ? 1 : 0.6,
          }}
        >
          Pause Audio
        </button>
      </div>

      {/* Loading Indicator */}
      {!isIframeReady && !error && <p>Loading audio...</p>}

      {/* Error Message */}
      {error && <p style={{ color: 'red' }}>{error}</p>}
    </div>
  );
};

export default YouTubeAudioPlayer;
